*,
*::after,
*::before {
  box-sizing: border-box;
}

:root {
  --body-bg: #fff;
  --accent-color: #1e87f0;
  --main-bg:  #fff;
  --main-color: #5a5656;

  /* --body-bg: #22272e;
  --accent-color: #2d333b;
  --main-bg:  #2d333b;
  --main-color: #fff; */
}

body {
  background-color: var(--body-bg);
  width: 100%;
  min-height: 100vh;
  padding-bottom: 100px;
}

button {
  border: none;
  cursor: pointer;
}